<template>
  <div class = "header">
    <div class = "header-left">
      <div class = "iconfont back-icon">&#xe624;</div>
    </div>
    <div class = "header-input">
      <span class = "iconfont">&#xe632;</span>
      输入城市/景点/游玩/主题
    </div>
    <router-link to ='/city'>
    <div class = "header-right">
      {{this.$store.state.city}}
      <span class = "iconfont arrow-icon">&#xe674;</span>
    </div>
    </router-link>
  </div>
</template>

<script>
    export default {
        name: "HomeHeader",
        props :{
          city: String
        }
    }
</script>

<style lang = "stylus" scoped>
  // 1rem = 50 px
  //@import '~@/assets/styles/varibles.styl'
  @import '~styles/varibles.styl'
  .header
    display:flex
    line-height:.86rem
    height:.86rem
    background-color:$bgColor
    color:#fff
    .header-left
      width:.64rem
      float:left
      .back-icon
        text-align :center
        font-size:.4rem
    .header-right
      min-width:1.04rem
      padding:0.1rem
      float:right
      text-align:center
      color:#fff
      .arrow-icon
        margin-left :-.04rem
        font-size:.24rem
    .header-input
      flex:1
      height:.64rem
      line-height:.64rem
      margin-top:.12rem
      margin-left:.2rem
      padding-left:.2rem
      background-color:#fff
      border-radius:.1rem
      color:#ccc

</style>



